.container {
	max-width: 120rem;
	margin: 8rem auto;
	background-color: var(--color-grey-light-1);
	box-shadow: var(--shadow-dark);

	min-height: 50rem;

	@media only screen and (max-width: $bp-largest) {
		margin: 0;
		width: 100%;
		max-width: 100%;
	}
}

.header {
	font-size: 1.4rem;
	height: 7rem;
	background-color: #fff;
	border-bottom: var(--line);

	display: flex;
	justify-content: space-between;
	align-items: center;

	@media only screen and (max-width: $bp-small) {
		flex-wrap: wrap;
		align-content: space-around;
		height: 10rem;
		padding: .5rem 0;
	}
}

.content {
	display: flex;

	@media only screen and (max-width: $bp-medium) {
		flex-direction: column;
	}
}

.sidebar {
	background-color: var(--color-grey-dark-1);

	flex: 0 0 18%;

	display: flex;
	flex-direction: column;
	justify-content: space-between;
}

.hotel_view {
	background-color: #fff;
	flex: 1;
}

.detail {
	font-size: 1.4rem;
	display: flex;
	padding: 4.5rem;
	background-color: var(--color-grey-light-1);
	border-bottom: var(--line);

	@media only screen and (max-width: $bp-medium) {
		padding: 3rem;
	}

	@media only screen and (max-width: $bp-small) {
		flex-direction: column;
		padding: .5rem;
	}
}

.description {
	background-color: #fff;
	box-shadow: var(--shadow-light);
	padding: 3rem;
	flex: 0 0 60%;
	margin-right: 4.5rem;

	@media only screen and (max-width: $bp-medium) {
		padding: 2rem;
		margin-right: 3rem;
		margin-top: 2rem;
	}

	@media only screen and (max-width: $bp-small) {
		padding: .5rem;
		padding-bottom: 3rem;
		margin-right: 0;
		margin-bottom: 3rem;
	}
}

.userReviews {
	flex: 1;
	display: flex;
	flex-direction: column;
	align-items: center;
}
